html,
body {
  height: 100vh;
  width: 100%;
}

.calendar-screen {
  display: flex;
  flex-flow: column;
  height: 100vh;
}

.rbc-calendar {
  height: 100%;
}

.react-datetime-picker input:focus {
  outline: none;
}

.react-datetime-picker__wrapper {
  border: 0px solid gray !important;
}

.fab {
  border-radius: 99px;
  bottom: 25px;
  font-size: 10px;
  padding: 10px;
  right: 25px;
  position: fixed;
}

.fab-danger {
  bottom: 25px;
  position: fixed;
  left: 25px;
}


.center-div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}